"use client";

import Image from 'next/image';
import { notFound } from 'next/navigation';
import { useState } from 'react';
import { Minus, Plus } from 'lucide-react';
import { getProductById, getAllProducts } from '@/lib/data';
import { Button } from '@/components/ui/button';
import { useCart } from '@/context/cart-context';
import { RelatedProducts } from '@/components/related-products';
import { Separator } from '@/components/ui/separator';

export default function ProductDetailPage({ params }: { params: { id: string } }) {
  const [quantity, setQuantity] = useState(1);
  const { addToCart } = useCart();
  
  const product = getProductById(params.id);
  const allProducts = getAllProducts();

  if (!product) {
    notFound();
  }
  
  const handleAddToCart = () => {
    for (let i = 0; i < quantity; i++) {
        addToCart({
            id: product.id,
            name: product.name,
            price: product.price,
            imageUrl: product.imageUrl,
        });
    }
  };

  return (
    <div className="container mx-auto max-w-6xl px-4 py-8 md:py-12">
      <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
        <div className="relative aspect-square w-full rounded-lg overflow-hidden shadow-lg">
          <Image
            src={product.imageUrl}
            alt={product.name}
            fill
            className="object-cover transition-transform duration-300 hover:scale-105"
            data-ai-hint={product.imageHint}
            sizes="(max-width: 768px) 100vw, 50vw"
          />
        </div>
        <div className="flex flex-col justify-center">
          <h1 className="text-3xl md:text-4xl font-headline font-bold mb-2">{product.name}</h1>
          <p className="text-muted-foreground mb-4">{product.category}</p>
          <p className="text-3xl font-bold text-primary mb-6">₹{product.price.toFixed(2)}</p>
          <p className="text-foreground/80 leading-relaxed mb-6">{product.description}</p>
          
          <div className="flex items-center gap-4 mb-6">
            <div className="flex items-center border rounded-md">
              <Button variant="ghost" size="icon" onClick={() => setQuantity(q => Math.max(1, q - 1))}>
                <Minus className="h-4 w-4" />
              </Button>
              <span className="w-12 text-center font-medium">{quantity}</span>
              <Button variant="ghost" size="icon" onClick={() => setQuantity(q => q + 1)}>
                <Plus className="h-4 w-4" />
              </Button>
            </div>
            <Button variant="accent" size="lg" onClick={handleAddToCart}>
              Add to Cart
            </Button>
          </div>

          <div className="text-sm text-muted-foreground">
            {product.stock > 0 ? (
              <p>In Stock: <span className="font-semibold text-green-600">{product.stock} items available</span></p>
            ) : (
              <p className="font-semibold text-red-600">Out of Stock</p>
            )}
          </div>
        </div>
      </div>
      
      <Separator className="my-12 md:my-16"/>

      <RelatedProducts currentProduct={product} allProducts={allProducts} />

    </div>
  );
}
