import { getRelatedProducts } from "@/ai/flows/related-product-recommendations";
import { ProductCard } from "./product-card";
import type { Product } from "@/lib/types";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel"

interface RelatedProductsProps {
    currentProduct: Product;
    allProducts: Product[];
}

export async function RelatedProducts({ currentProduct, allProducts }: RelatedProductsProps) {
    const otherProducts = allProducts.filter(p => p.id !== currentProduct.id);
    const otherProductNames = otherProducts.map(p => p.name);

    let recommendedProducts: Product[] = [];

    try {
        const recommendations = await getRelatedProducts({
            productDescription: currentProduct.description,
            productNames: otherProductNames,
        });

        recommendedProducts = recommendations.relatedProducts
            .map(name => otherProducts.find(p => p.name === name))
            .filter((p): p is Product => p !== undefined)
            .slice(0, 5); // Limit to 5 recommendations

    } catch (error) {
        console.error("Failed to get related products:", error);
        // Fallback to showing products from the same category
        recommendedProducts = allProducts.filter(p => p.category === currentProduct.category && p.id !== currentProduct.id).slice(0, 5);
    }
    
    if (recommendedProducts.length === 0) {
        return null;
    }

    return (
        <section>
            <h2 className="text-2xl md:text-3xl font-headline font-bold mb-8 text-center">You Might Also Like</h2>
            <Carousel
                opts={{
                    align: "start",
                    loop: true,
                }}
                className="w-full"
            >
                <CarouselContent>
                    {recommendedProducts.map((product) => (
                        <CarouselItem key={product.id} className="md:basis-1/2 lg:basis-1/3 xl:basis-1/4">
                             <div className="p-1">
                                <ProductCard product={product} />
                            </div>
                        </CarouselItem>
                    ))}
                </CarouselContent>
                <CarouselPrevious className="hidden md:flex"/>
                <CarouselNext className="hidden md:flex"/>
            </Carousel>
        </section>
    );
}
